.app {
  background: url("./assets/bg2.jpg");
  background-size: cover;
  height: 100vh;
  display: flex;
  color: white;
}

.main {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), #4d4c4c),
    url("./assets/bg.jpg") center;
  background-size: cover;
  background-size: cover;
  width: 80%;
  display: flex;
  flex-direction: column;
}

.pyramid {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), #272727);
  background-size: cover;

  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.moneyList {
  padding: 20px;
  list-style: none;
  width: 50%;
}

.moneyListItem {
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
}

.moneyListItem.active {
  background-color: teal;
}

.moneyListItemNumber {
  width: 30%;
  font-size: 18px;
  font-weight: 400;
}

.moneyListItemAmount {
  font-size: 20px;
  font-weight: 400;
}

.top {
  height: 60%;
  position: relative;
}

.timer {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 5px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 10px;
  left: 80px;
  font-size: 30px;
  font-weight: 700;
}

.bottom {
  height: 35%;
}

.trivia {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.question {
  width: 50%;
  background: linear-gradient(#4d4d4d, #272727); /* Darker background color */
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  border: 2px solid white;
  font-size: 20px;
}

.answers {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.answer {
  width: 40%;
  padding: 10px;
  margin: 0 10px 20px 10px;
  text-align: center;
  background: linear-gradient(#3a3a3a, #272727); /* Darker background color */
  border: 1px solid white;
  border-radius: 15px;
  font-weight: 300;
  font-size: 20px;
  cursor: pointer;
}

.answer:hover,
.answer.active {
  background: #535353; /* Slightly lighter color on hover/active */
}

.answer.correct {
  animation: correct 3s ease forwards;
}

@keyframes correct {
  0%,
  22%,
  42% {
    background: #535353; /* Slightly lighter color during animation */
  }
  20%,
  40%,
  60% {
    background: linear-gradient(#3a3a3a, #272727);
  }
  62%,
  100% {
    background: #008000; /* Green for correct answer */
  }
}

.answer.wrong {
  animation: wrong 3s ease forwards;
}

@keyframes wrong {
  0%,
  22%,
  42% {
    background: #535353; /* Slightly lighter color during animation */
  }
  20%,
  40%,
  60% {
    background: linear-gradient(#3a3a3a, #272727);
  }
  62%,
  100% {
    background: #dc143c; /* Crimson for wrong answer */
  }
}

.endText {
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.start {
  width: 250px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.startInput {
  width: 100%;
  height: 30px;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
  background-color: #535353; /* Slightly lighter color for input background */
  color: white;
}

.startInput:focus {
  outline: none;
}

.startButton {
  width: 100%;
  height: 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  background-color: teal;
  color: white;
  transition: background 0.3s ease-in-out;
}

.startButton:hover {
  background: #27ae60;
}
