.start {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient url("../assets/bg.jpg");
  background-size: cover;
}

.startInput {
  width: 250px;
  height: 40px;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
  margin-bottom: 20px;
  padding: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.startInput:focus {
  outline: none;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.startButton {
  width: 250px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  background-color: teal;
  color: white;
  transition: background 0.3s ease-in-out;
}

.startButton:hover {
  background-color: rgb(2, 88, 88);
}
